<template>
  <v-app>
    <v-content>
      <v-container class="page-container">
        <Header/>
        <v-layout justify-center row style="margin-top: 3em;">
          <v-flex xs3 text-xs-left>
            <Selection ref="Selection"/>
          </v-flex>
          <v-flex xs9>
            <PublicationList ref="Papers" headline=""/>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import PublicationList from './components/PublicationList'
import Header from './components/Header'
import Selection from './components/Selection'
import publications from './assets/publications.yaml'
export default {
  name: 'PublicationsPage',
  components: {
    PublicationList,
    Header,
    Selection
  },
  data () {
    return {
      filters: []
    }
  },
  created () {
    let attrs = ['year', 'type', 'keywords']
    let histograms = {}
    // for (let attr of attrs) {
    //   histograms[attr] = getHistogram(publications.map(p => ))
    // }
    this.filters[0] = {name: 'year', selections: publications.map(p => p.year), sort: 'name', order: 'desc'}
    this.filters[1] = {name: 'paper type', selections: publications.map(p => p.type)}
    // let authors = publications.map(p => p.authors).reduce((pre, cur) => pre.concat(cur))
    // authors = authors.map(author => author.replace('*', ''))
    let keywords = publications.map(p => p.keywords).reduce((pre, cur) => pre.concat(cur))
    this.filters[2] = {name: 'keywords', selections: keywords}
  },
  mounted() {
    this.$refs.Selection.setTotal(publications.length)
    this.$refs.Selection.addFilters(this.filters)
    this.$refs.Papers.update(publications)
  }
}
</script>





