 <template>
  <div>
  <template v-for="filter in filters">
    <v-list class="pa-3" :key=filter.name>
      <strong>{{filter.name}}</strong>
      <template v-for="(selection, index) in filter.selections">
        <v-list-tile
          :key=index
        >
          <v-list-tile-content style="font-size: 0.9em">
            <v-list-tile-title>
              {{ selection.name }} ({{ selection.count }})
            </v-list-tile-title>
            <v-list-tile-sub-title>
              <v-progress-linear
                color="blue darken-2"
                height="2"
                :value=calcPercentage(selection.count)
                class="pa-0 ma-0"
              ></v-progress-linear>
            </v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
    </v-list>
  </template>
  </div>
</template>

<script>

export default {
  name: 'Selection',
  data () {
    return {
      total: 1,
      filters: []
    }
  },
  methods: {
    setTotal (total) {
      this.total = total
    },
    addFilters (filters) {
      for (let filter of filters) {
        let sortKey = filter.sort || 'count'
        let sortFn = (filter.order === 'asc') 
          ? (a, b) => a[sortKey] - b[sortKey]
          : (a, b) => b[sortKey] - a[sortKey]

        filter.selections = this.getHistogram(filter.selections).sort(sortFn)
      }
      this.filters = this.filters.concat(filters)
    },
    calcPercentage (count) {
      return count / this.total * 100
    }, 
    getHistogram (array) {
      let histogram = {}
      for(let item of array) {
        if (histogram.hasOwnProperty(item)) {
          histogram[item] += 1
        } else {
          histogram[item] = 1
        }
      }
      return Object.keys(histogram).map(k => { return {name: k, count: histogram[k]} })
    }
  }
}
</script>


