<template>
  <v-toolbar flat dense color="white">
    <img width=30 src="/static/image/me.jpg" style="border-radius: 50%"/>
    <v-toolbar-title class="ma-2">
      JPKLi
    </v-toolbar-title>
    <v-flex style="margin-left: 3em" text-xs-left>
      <v-btn
        v-for="link in links"
        :key="link.label"
        flat
        :class="{active: link.active}"
        :href="link.url"
        style="text-transform: none;"
      >
        {{ link.label }}
      </v-btn>
    </v-flex>
  </v-toolbar>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      links: [
        {label: 'Home', url: '/', active: false},
        {label: 'Projects', url: '/projects', active: false},
        {label: 'Publications', url: '/publications', active: false},
        {label: 'Talks', url: '/talks', active: false}
      ]
    }
  },
  mounted () {
    let path = window.location.pathname
    this.links.filter(link => link.url == path)[0].active = true
  }
}
</script>

<style scoped>

.active {
  border-bottom: 3px solid #bbb;
}
</style>



