<template>
  <v-layout id="profile" wrap justify-center row pa-2>
  <v-flex pa-2 xs12 sm3 style="margin: auto; max-width: 200px;">
      <v-img class="portrait" width="auto" :src="profile.photo" />

  </v-flex>
  <v-flex grow xs12 sm9>
    <v-card flat>
      <v-card-title class="pa-2 display-1" primary-title>
        {{profile.greeting}}
      </v-card-title>
      <template v-for="(intro, index) in profile.introductions">
        <v-card-text :key=index class="pa-1">
          <p class="text-xs-left ma-1" v-html="intro"></p>
        </v-card-text>
      </template>
      <v-card-actions>
        <!-- <v-spacer></v-spacer> -->
        <v-icon small class="ma-1" color="blue darken-2">email</v-icon>
        <a href="mailto:jpkelvin@gmail.com">jpkelvin@gmail.com</a>
        &nbsp;
        &nbsp;
        &nbsp;
        <v-icon small class="ma-1" color="blue darken-2">fa fa-github</v-icon>
        <a href="https://github.com/jpkli">jpkli</a>

      </v-card-actions>
    </v-card>
  </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: 'Profile',
  data () {
    return {
      profile: {greeting: '', introductions: []}
    }
  },
  methods: {
    update (profile) {
      this.profile = profile
    }
  }
}
</script>

<style scoped>
#profile {
  background-color: #fff;
}
</style>


