<template>
  <v-app id="homepage">
    <!-- <v-toolbar app></v-toolbar> -->
    <v-content>
      <v-container class="page-container">
        <Profile ref="Profile" />
        <v-flex text-xs-left>
          <h2>
            Featured Projects
            <a href="/projects" class="title-link"> >> see all projects</a>
          </h2>
          <Projects ref="FeaturedProjects"/>
        </v-flex>
        <div>
        <v-layout justify-center row wrap>
          <v-flex md8 sm7 xs12 text-xs-left>
            <h2>
              Selected Publications
              <a href="/publications" class="title-link"> >> see all publications</a>
            </h2>
            <Publications ref="SelectedPapers"/>
          </v-flex>
          <v-flex md4 sm5 xs12 text-xs-left>
            <h2>
              Talks
              <a href="/talks" class="title-link"> >> view on map</a>
            </h2>
            <Talks ref="Talks"/>
          </v-flex>
        </v-layout>
        </div>
      </v-container>
    </v-content>
    <!-- <v-footer
    id="footer"
    height="auto"
    color="gray"
    >
     <v-toolbar flat dense class="page-container">
        <v-btn
          v-for="link in links"
          :key="link"
          flat
          round
          style="text-transform: none;"
        >
          {{ link }}
        </v-btn>
        <v-spacer></v-spacer>

        <v-flex text-xs-right>
          &copy;2019 — <strong>Jianping Kelvin Li</strong>
        </v-flex>

      </v-toolbar>
    </v-footer> -->
  </v-app>
</template>

<script>
import Profile from './components/Profile'
import Projects from './components/ProjectList'
import Publications from './components/PublicationList'
import Talks from './components/PresentationList'

import profile from './assets/profile.yaml'
import projects from './assets/projects.yaml'
import publications from './assets/publications.yaml'
import talks from './assets/talks.yaml'

export default {
  name: 'Home',
  components: {
    Profile,
    Projects,
    Publications,
    Talks
  },
  data () {
    return {
      links: [
        'All Porjects',
        'All Publications',
        'All Talks'
      ]
    }
  },
  mounted () {
    this.$refs.Profile.update(profile)
    this.$refs.FeaturedProjects.update(projects.filter(p => p.featured))
    this.$refs.SelectedPapers.update(publications.filter(p => p.featured))
    this.$refs.Talks.update(talks)

  }
}
</script>

<style scoped>
#homepage {
  background-color: #fff;
}

.title-link {
  font-size: 0.6em;
  color: #777;
  padding-left: 10px;
}
</style>



