<template>
  <v-app>
    <v-content>
      <v-container class="page-container">
        <Header/>
        <v-layout justify-center row  style="margin-top: 3em">
          <v-flex xs10 pa-0>
            <ProjectList ref="Projects" headline=""/>
          </v-flex>
          <v-flex xs2 text-xs-left>
             <Selection ref="Selection"/>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import ProjectList from './components/ProjectList'
import Header from './components/Header'
import projects from './assets/projects.yaml'
import Selection from './components/Selection'

export default {
name: 'ProjectPage',
  components: {
    ProjectList,
    Header,
    Selection
  },
  data () {
    return {
      filters: []
    }
  },
  created () {
    let languages = projects.map(p => p.languages).reduce((pre, cur) => pre.concat(cur))
    this.filters[0] = {name: 'Languages', selections: languages}

    // let libraries = projects.filter(p => p.libraries).map(p => p.libraries).reduce((pre, cur) => pre.concat(cur))
    // this.filters[1] = {name: 'Libraries/Frameworks', selections: libraries}

  },
  mounted() {
    this.$refs.Projects.update(projects)
    this.$refs.Projects.panelView = true

    this.$refs.Selection.setTotal(projects.length)


    this.$refs.Selection.addFilters(this.filters)
  }
}
</script>

<style>
</style>



