<template>
  <v-app>
    <v-content>
      <v-container class="page-container">
        <Header/>
        <v-layout justify-center row  style="margin-top: 3em">
          <v-flex xs8 pa-0>
            <div id="wmap"></div>
          </v-flex>
          <v-flex xs4 text-xs-left>
             <PresentationList ref="Talks" headline=""/>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import PresentationList from './components/PresentationList'
import Header from './components/Header'
import talks from './assets/talks.yaml'
import pplot from '../../p.plot/'

export default {
  name: 'TalkPage',
  components: {
    PresentationList,
    Header,
  },
  data () {
    return {
      filters: []
    }
  },
  mounted() {

    let data = {
        json: [
          {code: 'USA', count: 4},
          {code: 'JPN', count: 1},
          {code: 'GBR', count: 1},
          {code: 'CHN', count: 1},
          {code: 'FRA', count: 1},
          {code: 'MLI', count: 0},
        ],
        join: {
            field: 'code',
            type: 'code'
        },
        vmap: {
            color: 'count'
        }
    };

    let view = {
        container: '#wmap',
        width: 600,
        height: 400,
        scale: 90,
        exponent: 0.5,
        defaultColor: '#ddd',
        projection: 'Mercator',
        padding: {left: 50, right: 50, top: 50, bottom: 50},
    }

    let testMap = new pplot.GeoMap(data, view).render();
    talks.forEach(talk => {
      testMap.addMarker({
        coordinate: talk.coordinate,
        title: [talk.date, talk.for, talk.location].join(', ')
      });
    })
    
    this.$refs.Talks.update(talks)
  }
}
</script>

<style>
i:hover {
  color: #E00!important;
}
</style>



