<template>
  <div>
    <h2 v-if="headline" class="title text-xs-left">{{headline}}</h2>
    <v-layout wrap row style="background-color: #fff; text-align: left;">
      <template v-for="(project, index) in projects">
        <v-flex :class="{md4: panelView, md6: !panelView}" :key=index>
          <v-card flat class="ma-1 pa-2" :class="{frame: panelView}">
            <v-card-title class="pa-0">
              <div class="project-title">{{project.name}}</div>
            </v-card-title>
            <v-layout wrap row>
              <v-flex :class="{xs5: !panelView, xs12: panelView}">
                <v-img
                  class="thumb"
                  height=138
                  v-bind:src="project.img"
                />
              </v-flex>
              <v-flex :class="{xs7: !panelView, xs12: panelView}">
                <v-card-text class="pa-2 caption project-text" :class="{'tall-text': panelView}">{{project.description}}</v-card-text>
                <v-card-text class="project-icons" color="blue darken-2">
                  <a v-if="project.paper" :href="project.paper"><v-icon color="blue darken-2" title="paper">fa fa-file-pdf-o</v-icon>paper</a>
                  <a v-if="project.website" :href="project.website"><v-icon color="blue darken-2" title="homepage">fa fa-link</v-icon>website</a>
                  <a v-if="project.video" :href="project.video"><v-icon color="blue darken-2" title="video">fa fa-film</v-icon>video</a>
                  <a v-if="project.code" :href="project.code"><v-icon color="blue darken-2" title="code">fa fa-github</v-icon>code</a>
                  <a v-if="project.demo" :href="project.demo"><v-icon color="blue darken-2" title="demo">fa fa-laptop</v-icon>demo</a>
                  <a v-if="project.slides" :href="project.slides"><v-icon color="blue darken-2" title="demo">fa fa-indent</v-icon>slides</a>
                </v-card-text>
              </v-flex>
            </v-layout>
          </v-card>
        </v-flex>
      </template>
    </v-layout>
  </div>
</template>

<script>
export default {
  name: 'ProjectList',
  props: {
    headline: ''
  },
  data () {
    return {
      projects: [],
      panelView: false
    }
  },
  methods: {
    update (projects) {
      this.projects = projects
    }
  }
}
</script>

<style>
a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.frame {
  border: 1px solid #ccc!important;
  min-height: 23em;
}

.project-title {
  font-weight: bold;
  font-size: 1.2em;
  text-align: left;
  /* margin: 0 auto; */
  /* margin-left: 0.5em; */
}

.thumb div {
  object-fit: cover;
  min-height: 128px;
  background-position: left!important;
	-webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .5s ease-in-out;
	transition: .5s ease-in-out;
}

.thumb:hover div{
	background-position: right!important;
}

.project-text {
  text-align: left;
  min-height: 9em;
  /* font-size: 0.9em; */
  /* margin: 0 0 0 1.2em; */
}

.tall-text {
  min-height: 11em;
}

.project-icons {
  padding: 0.2em 0.8em;
  text-align: left;
  font-size: 0.88em;
}

.project-icons a {
  margin-right: 0.5em;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.project-icons i {
  margin-right: 0.3em;
  font-size: 1em;
}

</style>



